define('oxide/helpers/round-number', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports.round = round;

    function round(input, points) {
        if (points === undefined) {
            points = 2;
        }
        return parseFloat(input).toFixed(points);
    }exports['default'] = Ember['default'].Handlebars.makeBoundHelper(round);

});